.app-wrapper {
    font-family: sans-serif;
    width: 1200px;
    margin: 0 auto;
    display: grid;
    grid-template-areas:
            "h h"
            "n c";
    grid-template-rows: 80px auto;
    grid-template-columns: 2fr 10fr;
    grid-gap: 10px;
}

.app-wrapper-content {
    border-top: solid;
    border-left: solid;
    border-color: aquamarine;
    grid-area: c;
}




